import React from 'react';

export const SignalIcon = ({ strength = 4, className = "" }: { strength?: number; className?: string }) => {
  // Signal strength typically 1-4 bars
  const validStrength = Math.max(0, Math.min(4, strength));
  
  // Opacity logic for bars based on strength
  // Bar 1: Always visible if strength >= 1
  // Bar 2: Visible if strength >= 2
  // Bar 3: Visible if strength >= 3
  // Bar 4: Visible if strength >= 4
  
  const getOpacity = (barIndex: number) => {
    return validStrength >= barIndex ? 1 : 0.3; // 0.3 opacity for inactive bars
  };

  return (
    <svg width="20" height="13" viewBox="0 0 20 13" fill="none" xmlns="http://www.w3.org/2000/svg" className={className}>
      {/* Bar 4 (Tallest) */}
      <path opacity={getOpacity(4)} fillRule="evenodd" clipRule="evenodd" d="M19.2 1.14623C19.2 0.513183 18.7224 0 18.1333 0H17.0667C16.4776 0 16 0.513183 16 1.14623V11.0802C16 11.7132 16.4776 12.2264 17.0667 12.2264H18.1333C18.7224 12.2264 19.2 11.7132 19.2 11.0802V1.14623Z" fill="currentColor"/>
      
      {/* Bar 3 */}
      <path opacity={getOpacity(3)} fillRule="evenodd" clipRule="evenodd" d="M11.7659 2.44528H12.8326C13.4217 2.44528 13.8992 2.97078 13.8992 3.61902V11.0527C13.8992 11.7009 13.4217 12.2264 12.8326 12.2264H11.7659C11.1768 12.2264 10.6992 11.7009 10.6992 11.0527V3.61902C10.6992 2.97078 11.1768 2.44528 11.7659 2.44528Z" fill="currentColor"/>
      
      {/* Bar 2 */}
      <path opacity={getOpacity(2)} fillRule="evenodd" clipRule="evenodd" d="M7.43411 5.09433H6.36745C5.77834 5.09433 5.30078 5.62652 5.30078 6.28301V11.0377C5.30078 11.6942 5.77834 12.2264 6.36745 12.2264H7.43411C8.02322 12.2264 8.50078 11.6942 8.50078 11.0377V6.28301C8.50078 5.62652 8.02322 5.09433 7.43411 5.09433Z" fill="currentColor"/>
      
      {/* Bar 1 (Shortest) */}
      <path opacity={getOpacity(1)} fillRule="evenodd" clipRule="evenodd" d="M2.13333 7.53962H1.06667C0.477563 7.53962 0 8.06421 0 8.71132V11.0547C0 11.7018 0.477563 12.2264 1.06667 12.2264H2.13333C2.72244 12.2264 3.2 11.7018 3.2 11.0547V8.71132C3.2 8.06421 2.72244 7.53962 2.13333 7.53962Z" fill="currentColor"/>
    </svg>
  );
};
